import React from 'react'
import { Link } from 'react-router-dom'

const Login = () => {
  return (
  <>
  <div className="loginpage">
    <div className="container">
        <div className="row">
            <div className="col-lg-12 col-md-12 logo">
                <img src="../../images/logo.png" alt="logo" />
             </div>
             <div className="col-xl-6 col-lg-6 col-md-6 col-12 pt-5">
              <img src="../../images/loginscreen.jpg" alt="login screen " className="img-fluid" />
</div>
             <div className="col-xl-6 col-lg-6 col-md-6 col-12 pt-5">
              <h3> Welcome to HealthAssure! </h3>
              <form className="loginform">
              <div class="input-group mb-3 ">
  <span className="input-group-text" id="basic-addon1"> <i className="fa-solid fa-user"></i> </span>
  <input type="text" className="form-control" placeholder="Email id or Phone Number" />
</div>
<div class="input-group mb-3 ">
  <span className="input-group-text" id="basic-addon1"> <i class="fa-solid fa-lock"></i> </span>
  <input type="text" className="form-control" placeholder="Password" />
</div>
<div class="mb-3 form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1" checked />
    <label class="form-check-label" for="exampleCheck1">Stay signed in</label>
  </div>
<Link to="/dashboard"><button className="btn button_submit"> Login </button> </Link>
              </form>
            </div>
        </div>
    </div>
  </div>
  </>
  )
}

export default Login