import Dropdown from 'react-bootstrap/Dropdown';
import React from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from '@mui/material/Button';
import { DateRangePicker } from 'rsuite';
import subDays from 'date-fns/subDays';
import addDays from 'date-fns/addDays';
import ChartComponent from './ChartComponent';

const Dashboard = () => {

  return (
   <>
   <section className="dashboard">
   <div className="container-fluid header">
    <div className="row">
      <div className="col-xl-1  col-lg-1 col-md-1 col-12 logo">
     <Link to="/" > <img src="../../images/logo.png" alt="logo" /></Link>
    </div>
      <div className="col-xl-9  col-lg-9 col-md-9 col-12 pt-4 text-center">
     <Link to="/dashboard"> <Button variant="outlined" size="medium" className="btnactive mx-2">
Dashboard </Button> </Link>
<Button variant="outlined" size="medium" className="btninactive "> All About Users </Button>
<Button variant="outlined" size="medium" className="btninactive "> Account Details </Button>
<Link to="/utilization"> <Button variant="outlined" size="medium" className=" btninactive"> Utilization </Button> </Link>
    </div>
      <div className="col-xl-1  col-lg-1 col-md-1 col-12 pt-4">
<Dropdown>
<Dropdown.Toggle variant="success" id="dropdown-basic" className="logout">   
     <h6> Mr Prakash <span> <i className="fa-solid fa-chevron-down"></i>  </span> </h6>
     <p> Indus Tower  </p>
      </Dropdown.Toggle>
<Dropdown.Menu>
<Link to="/">  <Dropdown.Item> Logout  </Dropdown.Item> </Link>
</Dropdown.Menu>
</Dropdown>
 </div>
    </div>
 </div>
</section>

<section>
<div className="container-fluid shadow py-4 bg-white mt-4 dashboard-sec">
<div className="row">
<div className="col-lg-8 col-md-8 col-12">
  <form className="row">
  <div className="col-lg-4 col-xl-4"> 
<DateRangePicker placeholder={"Start Date - End Date"}  ranges={[]} hoverRange={date => [subDays(date, 1), addDays(date, 1)]} />
</div>
    <div className="col-lg-4 col-xl-4"> 
  <select className="form-select" aria-label="Default select example">
 <option value="0" selected disabled > Gender</option>
  <option value="2"> Male </option>
  <option value="3">Female</option>
</select>
</div>
<div className="col-lg-4 col-xl-4"> 
  <select className="form-select" aria-label="Default select example">
 <option value="0" selected disabled> Type of User  </option>
  <option value="Registered"> Registered </option>
  <option value="Active"> Active </option>
  <option value="All"> All </option>
</select>
</div>
  </form>

</div>
<div className="col-lg-4 col-md-4 col-12">
<Button variant="contained" size="medium" className="download_button float-end">
Download  </Button>
   
</div>
</div>
</div>

<div className="container mt-5">

<ChartComponent/>
  
</div>
</section>
   </>
  )
}

export default Dashboard